import Vue from 'vue'
import Router from 'vue-router'

// 承载
import myTestDetail from '../pages/myTestDetail.vue'
// 主页
import myTestDetailIndex from '../views/my-test-detail.vue'

Vue.use(Router)


const router = new Router({
  routes: [{
      path: '/',
      name: 'myTestDetail',
      component: myTestDetailIndex
    }
  ]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#myTestDetail',
  router,
  components: {
    myTestDetail
  },
  template: '<myTestDetail/>'
})



